import { NuqsAdapter } from "nuqs/adapters/next/app";
import type { ReactNode } from "react";
import { AiContextProvider } from "@/lib/context/use-ai";
import { ChatProvider } from "@/lib/context/use-chat";
import { PagefindProvider } from "@/lib/context/use-pagefind";
import { SearchContextProvider } from "@/lib/context/use-search";
import { ReactQueryProviders } from "./react-query";

// Try to import ConvexProvider, but don't fail if it's not available
let ConvexProvider: React.ComponentType<{ children: ReactNode }> | null = null;
try {
  const { ConvexProvider: ConvexProviderComponent } = require("./convex");
  ConvexProvider = ConvexProviderComponent;
} catch (error) {
  console.warn("Convex provider not available, continuing without it");
}

export function AppProviders({ children }: { children: ReactNode }) {
  const providers = [
    <SearchContextProvider key="search">
      <NuqsAdapter>
        <ReactQueryProviders>
          <PagefindProvider>
            <AiContextProvider>
              <ChatProvider>{children}</ChatProvider>
            </AiContextProvider>
          </PagefindProvider>
        </ReactQueryProviders>
      </NuqsAdapter>
    </SearchContextProvider>,
  ];

  // Add ConvexProvider if available
  if (ConvexProvider) {
    providers.unshift(<ConvexProvider key="convex">{providers}</ConvexProvider>);
  }

  return <>{providers}</>;
}
